import { FaArrowLeft, FaArrowRight} from 'react-icons/fa'
import Button from "./Button"
import PageBox from './PageBox'


const NavigationBar = ({pageNumber, pageUp, pageDown, pageSelect, size, reset}) => {
    return (
        <div className = 'navigation-bar'>

            {pageNumber !== 0 && <FaArrowLeft
            onClick = {pageDown}/> 
            }

            {pageNumber !== 0 && <PageBox pageSelect = {pageSelect} pageNumber = {pageNumber}/> 
            }

            {pageNumber === 0 ? <Button 
            color = 'green' 
            text = 'Start'
            onClick = {pageUp}/>
            : pageNumber !== size - 1 &&
                <FaArrowRight onClick = {pageUp} />
            }

            {pageNumber === size - 1 &&<Button 
            text = 'End'
            onClick = {reset}/>
            }
        </div>
    )
}

export default NavigationBar
