import {FaDotCircle, FaRegDotCircle } from 'react-icons/fa'
import { BsDot } from 'react-icons/bs'
import {GoPrimitiveDot} from 'react-icons/go'
import PageComponent from './PageComponent'


const PageBox = ({pageSelect, pageNumber, size}) => {
    const pages = [1, 2, 3]
    var pageList = pages.map(function(page){
        return <PageComponent id = {page} pageNumber = {pageNumber} pageSelect = {pageSelect}/>
            })

    return (
        <div className = 'page-box'>{ pageList }</div>
    )
}

export default PageBox
