import './App.css';
import { useState } from 'react';
import ModuleContents from './components/ModuleContents'
import image0 from './images/image0.png'
import image1 from './images/image1.png'
import image2 from './images/image2.jpg'
import NavigationBar from './components/NavigationBar';
//import ArrowForwardIosRoundedIcon from '@material-ui/icons/ArrowForwardIosRounded';


function App() {

  const [count, setCount] = useState(0)

  const contents = [
    {pageNumber : 0,
    title: "Module Start",
    image: image0,
    text: '',
    color: '#5caef9',
    },
    {pageNumber : 1,
      title: "Module page 1",
      image: image1,
      text: 'this is page 1',
      color: '#be276b',
      },
    {pageNumber : 2,
      title: "Module page 2",
      image: image2,
      text: 'this is page 2',
      color: '#6627be',
        },

      {pageNumber : 3,
        title: "Module page 3",
        image: image2,
        text: 'this is page 3',
        color: '#7637be',
          }
      ]

  
  const pageUp = () => {
    setCount(prevCount => prevCount + 1)
    console.log(contents.length)
  }

  const pageDown = () => {
    setCount(prevCount => prevCount - 1)
  }

  const reset = () => {
    setCount(0)
  }

  const pageSelect = (id) => {
    setCount(id)
  }


  return (
    <div style = {{backgroundColor : contents[count].color}} className="container">
      <ModuleContents contents= {contents[count]} size = {contents.length} pageUp = {pageUp} pageDown = {pageDown} reset = {reset}/>
      <NavigationBar className='header' pageNumber = {count} pageUp = {pageUp} pageDown = {pageDown} size = {contents.length} reset = {reset} pageSelect = {pageSelect}/>
    </div>
  );
}

export default App;
